.Profile {
  margin-bottom: 0.7rem;
  margin-left: 12.5rem;
}
.profile-container li.nodelist-item {
  width: 25rem;
  height: 17rem;
}
.profile-card button {
  display: block;
  width: 100%;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
}
.profile-card button:active,
.profile-card button:focus {
  outline: 0;
}
.Profile-avatar {
  width: 100%;
  text-align: center;
  margin-top: -8rem;
  border-radius: 50%;
}
.Profile-avatar img {
  position: relative;
  z-index: 5;
  border-radius: 50%;
  width: 9rem;
  height: 9rem;
  border: 2px solid #fafafa;
  cursor: pointer;
}
.ant-modal-content {
  margin-left: 12.5rem;
}
.ant-modal-body {
  padding: 0;
}
.Profile-header {
  height: 450px;
  width: 100%;
  margin-bottom: 0.5rem;
  overflow: hidden;
  background-color: #272727;
  cursor: default;
}
.Profile-header img {
  width: 100%;
}
.Profile-header-edit-icon {
  position: relative;
  display: inline-block;
  width: 100%;
  text-align: center;
  visibility: hidden;
  z-index: 6;
  top: 10rem;
  left: 0.5rem;
}
.Profile-info {
  margin-top: -2.3rem;
  background-color: #cfcccc;
  width: 100%;
  padding: 3rem 20rem 0.3rem;
  text-align: center;
}
.Profile-displayName {
  color: #272727;
  margin-bottom: 0.2rem;
  font-weight: bold;
}
.Profile-username {
  color: grey;
  font-size: 0.9rem;
}
.Profile-bio {
  color: #353535;
  padding-top: 0.2rem;
  padding-bottom: 1rem;
  margin-bottom: 0.1rem;
}
