.page-header {
  background-color: #272727;
  border-bottom: 0.1rem solid #313131;
  padding: 0;
  margin: 0;
  height: 3.1rem;
  line-height: 2.9rem;
  vertical-align: middle;
  width: 100%;
  overflow: hidden;
  z-index: 5;
}
.nav-search {
  padding: 0.45rem 1rem 0;
}
.nav-search-input {
  min-width: 75rem;
  padding: 0.07rem 0 0;
  margin: 0;
}
.nav-search-input input {
  background-color: #1b1b1b;
  color: #ffffff;
  border: 0.07rem solid transparent;
  border-left: 0.07rem solid #333333;
}
.nav-search-input input:hover {
  border: 0.07rem solid #333333;
}
.nav-search-input .ant-select-search__field__wrap input:focus-within {
  border: none;
  outline: none;
}
.nav-search-input input:focus-within {
  border: 0.07rem solid #16e998;
}
.ant-input-group-addon {
  border: 0.1rem solid #272727;
}
.ant-input-group-addon .ant-select-no-arrow {
  background-color: #1b1b1b;
  color: #ffffff;
  outline: none;
  border: 0.1rem solid #272727;
}
.ant-select-dropdown-menu {
  padding: 0;
  border-radius: 0.1rem;
}
.ant-select-dropdown-menu .ant-select-dropdown-menu-item {
  background-color: #dbd1bc;
  color: #272727;
}
.ant-select-dropdown-menu .ant-select-dropdown-menu-item:hover {
  background-color: #16e998;
}
.ant-select-dropdown-menu .ant-select-dropdown-menu-item-active {
  background-color: #16e998;
}
.ant-select-dropdown-menu .ant-select-dropdown-menu-item-disabled {
  height: 0;
  padding: 0;
  visibility: hidden;
}
ul.nav-list {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  overflow: hidden;
  padding: 0;
}
li.nav-item {
  display: inline;
  overflow: hidden;
  font-size: 0.9rem;
  padding: 0 0.3rem;
}
img.header-logo:hover {
  outline: 0;
}
.mode-toggle {
  padding-top: 0.1rem;
}
.create-node-button button.ant-btn {
  background: linear-gradient(90deg, #ff8fc3 0%, #f9f7bc 47%, #9eefff 100%);
  border: 0.02rem solid #f9f7bc;
}
.create-node-button:hover button.ant-btn,
.create-node-button:active button.ant-btn,
.create-node-button:focus button.ant-btn {
  background: linear-gradient(90deg, #ff8fc3 0%, #f9f7bc 47%, #9eefff 100%);
}
.io-enter-button {
  border: 0.04rem solid white;
  color: #f9f7bc;
}
.io-enter-button:hover {
  color: #16e998;
  border: 0.02rem solid #16e998;
}
a {
  color: white;
}
a:hover {
  color: #16e998;
}
.io-sider-button button.ant-btn {
  background-color: #272727;
  border: none;
  color: white;
  padding-bottom: 0.1rem;
}
.io-sider-button button.ant-btn:hover {
  background-color: #272727;
  border: none;
  color: #16e998;
}
@media only screen and (max-width: 1400px) {
  .nav-search-input {
    min-width: 50rem;
    padding: 0;
    margin: 0;
  }
}
@media only screen and (max-width: 1200px) {
  .nav-search-input {
    min-width: 45rem;
    padding: 0;
    margin: 0;
  }
}
@media only screen and (max-width: 800px) {
  .nav-search-input {
    min-width: 10rem;
    padding: 0;
    margin: 0;
  }
}
