.nodelist {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-content: flex-start;
  list-style: none;
  margin: auto;
  width: 100%;
  background-color: transparent !important;
  padding: 0.3rem 0.3rem;
  z-index: -15;
}
.node-card-icon {
  font-size: 5rem;
  color: #b8b8b8;
  display: block;
  text-align: center;
  padding: 3.7rem;
}
li.nodelist-item {
  flex: 1 1;
  display: block;
  text-align: left;
  height: 17rem;
  width: 29.3rem;
  overflow: hidden;
  background-color: #272727;
  margin: 0.25rem 0.25rem;
  color: #757575;
  box-shadow: 0 0 5px 0 #585858;
}
.nodelist-options {
  padding: 0.7rem 1.3rem 0.5rem;
  border-bottom: 0.09rem solid #3a3a3a;
}
div.nodelist-options-buttons {
  transition-delay: 0.1s;
  transition-duration: 0.3s;
  opacity: 1;
}
div.nodelist-options-buttons button {
  margin-left: 0.3rem;
  outline: #272727;
  color: white;
  background-color: transparent;
  border-radius: 0.3rem;
  border: none;
  cursor: pointer;
}
div.nodelist-options-buttons button:hover {
  color: #eeeeee;
  transition: 0.7s;
}
div.nodelist-options-buttons a {
  margin-left: 0.3rem;
  display: inline-block;
  color: white;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.nodelist-item:hover div.nodelist-options-buttons {
  opacity: 1;
  outline: transparent;
}
.nodelist-item-title {
  width: 82%;
  text-align: left;
  color: #272727;
  color: #16e998;
  font-size: 0.97rem;
  transition: all 0.3s;
  overflow: hidden;
  font-weight: normal;
  margin: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
}
input.nodelist-item-title {
  outline: none;
  color: #9e9e9e;
  background-color: #272727;
  border: none;
  font-size: 0.94rem;
  width: 15.3rem;
  overflow: hidden;
  white-space: normal;
  text-overflow: ellipsis;
}
.nodelist-item p {
  padding: 0.3rem 1.5rem;
  font-size: 1.07rem;
  font-style: italic;
  color: #b8b8b8;
  min-height: 100%;
}
.nodelist-collection-grid .node-card-icon {
  padding: 2rem;
  font-size: 3rem;
  border: 0.01rem solid #383838;
  height: 7.7rem;
}
.nodelist-collection-grid p {
  padding: 0.3rem 1.4rem;
  font-size: 0.7rem;
  background-color: #272727;
  border: 0.01rem solid #383838;
}
.nodelist-collection-grid p:hover {
  color: #b8b8b8;
}
.nodelist-collection-grid {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  list-style: none;
  padding: 0;
  margin: 0;
}
.nodelist-collection-grid li {
  width: 50%;
  height: 7.2rem;
  overflow: hidden;
}
.nodelist-collection-grid li.nodelist-collection-grid-full-width {
  width: 100%;
}
.nodelist-collection-item-single {
  height: 100%;
}
